main {
  position: relative;
  max-width: 100vw;
  min-height: 100vh;
  background: #0f0c29;  
  background: linear-gradient(to right, #24243e, #302b63, #0f0c29); 
  display: flex;
  justify-content: center;
  align-items: center;
}

.themeSwitcher {
  width: 11vw;
  padding: 0.5rem 1rem;
  height: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  right: 1%;
  top: 1%;
}
.themeSwitcher > i {
  cursor: pointer;
}
.card {
  width: 22%;
  background-color: var(--dark-card-theme);
  border-radius: 0.6rem;
  box-shadow: rgba(255, 255, 255, 0.24) 0px 3px 8px;
  margin: 5% 0;
  overflow: hidden;
}

.avater {
  width: 100%;
  height: 250px;
  object-fit: cover;
}
.card--body {
  padding: 0.5rem 1rem;
}
.name, .job, .link {
  text-align: center;
  margin: 0.5rem 0;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.name {
  font-size: 1.2rem;
}
.job {
  font-size: 0.9rem;
  color:#D35400;
}
.link a{
  font-size: 0.8rem;
  color:#297FE5;
  text-decoration: none;
  transition: all 0.5s ease-in-out;
  opacity: 0.8;
}
.link a:hover {
  text-decoration: underline;
  opacity: 1;
}
.btn--group {
  display: flex;
  justify-content: space-around;
}
.btn--group > a {
  color:#000;
  text-decoration: none;
}
.fa-at {
  color:#000;
}
.btn--group > a > button {
  border: none;
  border-radius: 5px;
  padding: 0.3rem 1rem;
  cursor: pointer;
  color:#000;
  font-weight: 600;
  font-size: 0.8rem;
  display: flex;
  align-items: center;
  background-color: #ECF0F1;
}
.btn--group > a > button > i {
  margin-right: 0.5rem;
}

.linkedinBtn {
  background-color: #5093E2!important;
}
.linkedinBtn {
  color:#fff!important;
}
.about {
  margin: 1rem 0;
}
.interests h3, .about > h3 {
  font-weight: normal;
}
.about > p , .interests > p {
  text-align: justify;
  text-justify: auto;
  opacity: 0.8;
  word-spacing: 2px;
  text-indent: 2rem;
  margin: 0.5rem 0;
  font-size: 0.85rem;
}
.card--footer {
  padding: 0.5rem 1rem;
  height: 5rem;
  background-color: #161619;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.card--footer > a > i {
  font-size: 1.2rem;
  color:#918E9B;
}
